@import "./libs/valiables";
@import "./libs/mixins";

// heading title
.heading-title {
	font-size: 0;
	width: 0;
	height: 0;
	overflow: hidden;
	margin: 0;
	position: absolute;
	visibility: hidden;
}

// header
.header {
	$icon-color: #fff;
	$icon-back-active-color: rgba(0,0,0,.2);

	position: fixed;
	z-index: 10;
	left: 0;
	right: 0;
	top: 0;
	height: $size-header-height-mobile;
	background: rgba(#000, .4);
	user-select: none;
	@include background-blur(5px);

	&__body {
		display: flex;
		justify-content: center;
	}
	&__logo {
		margin: 0;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		display: flex;
		justify-content: center;
		a {
			position: relative;
			z-index: 2;
			display: block;
			padding: 8px;
			@include button-touch-options();
		}
		img {
			display: block;
			width: 68px;
		}
	}
	&__navigation {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		width: $size-header-height-mobile;
		height: $size-header-height-mobile;
		> button {
			display: block;
			border: none;
			width: 100%;
			height: 100%;
			outline: none;
			background-color: rgba(#000, 0);
			transition: background-color 200ms ease-out;
			font-size: 0;
			@include button-touch-options();
			svg {
				display: block;
				position: absolute;
				left: 50%;
				top: 50%;
				color: $icon-color;
				pointer-events: none;
				transition: transform .3s ease-out;
				transform-origin: 50% 50%;
				&:nth-child(1) {
					transform: translate(-50%, -50%) scale(1);
				}
				&:nth-child(2) {
					transform: translate(-50%, -50%) scale(0);
				}
			}
			&:active {
				background: $icon-back-active-color;
			}
		}
		> div {
			position: relative;
			opacity: 0;
			pointer-events: none;
		}
		&.active {
			> button {
				background: $icon-back-active-color;
				svg {
					&:nth-child(1) {
						transform: translate(-50%, -50%) scale(0);
					}
					&:nth-child(2) {
						transform: translate(-50%, -50%) scale(1);
					}
				}
			}
			> div {
				opacity: 1;
				pointer-events: auto;
			}
		}
	}
	&__search {
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		width: $size-header-height-mobile;
		height: $size-header-height-mobile;
		> button {
			display: block;
			border: none;
			width: 100%;
			height: 100%;
			outline: none;
			background: rgba(#000, 0);
			transition: background .2s ease-out;
			font-size: 0;
			@include button-touch-options();
			svg {
				display: block;
				position: absolute;
				left: 50%;
				top: 50%;
				color: $icon-color;
				pointer-events: none;
				transition: transform .3s ease-out;
				transform-origin: 50% 50%;
				&:nth-child(1) {
					transform: translate(-50%, -50%) scale(1);
				}
				&:nth-child(2) {
					transform: translate(-50%, -50%) scale(0);
				}
			}
			&:active {
				background: $icon-back-active-color;
			}
		}
		> div {
			position: relative;
			opacity: 0;
			pointer-events: none;
		}
		&.active {
			> button {
				background: $icon-back-active-color;
				svg {
					&:nth-child(1) {
						transform: translate(-50%, -50%) scale(0);
					}
					&:nth-child(2) {
						transform: translate(-50%, -50%) scale(1);
					}
				}
			}
			> div {
				opacity: 1;
				pointer-events: auto;
			}
		}
	}

	&-navigation {
		ul {
			margin: 0;
			padding: 30px 0;
			box-sizing: border-box;
			list-style: none;
			position: fixed;
			left: 0;
			right: 0;
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			background: rgba(#666, .95);
		}
		li {
			position: relative;
			width: 50%;
		}
		a {
			display: block;
			box-sizing: border-box;
			padding: 10px 10px 10px 20px;
			text-decoration: none;
			color: #fff;
			font-size: 15px;
			font-weight: 600;
			font-family: $font-eng;
			border-radius: 0;
			-webkit-tap-highlight-color: transparent;
			&:active {
				text-decoration: underline;
			}
		}
		li.on {
			a {
				text-decoration: underline;
				cursor: default;
				background: rgba($color-key, .75);
			}
		}
	}
	&-search {
		form {
			margin: 0;
			position: fixed;
			left: 0;
			right: 0;
			padding: 0;
			background: rgba(#666, .95);
			display: flex;
			align-items: center;
		}
		fieldset {
			position: relative;
			margin: 0;
			padding: 0;
			border: none;
			flex: 1;
		}
		legend {
			font-size: 0;
			width: 0;
			height: 0;
			margin: 0;
			position: absolute;
			overflow: hidden;
		}
		span {
			position: relative;
			display: block;
			box-sizing: border-box;
			input {
				display: block;
				width: 100%;
				height: 54px;
				box-sizing: border-box;
				border: none;
				background: none;
				outline: none;
				color: #fff;
				padding: 0 20px;
				&::placeholder {
					color: rgba(#fff, .5);
				}
			}
			button {
				position: absolute;
				right: -10px;
				top: 0;
				display: block;
				width: 54px;
				height: 100%;
				background: none;
				border: none;
				font-size: 0;
				outline: none;
				transition: opacity .15s ease-out;
				pointer-events: none;
				opacity: 0;
				svg {
					color: #ccc;
					width: 18px;
					height: 18px;
				}
				&:active {
					opacity: .5;
				}
			}
			&.is-word {
				input {
					padding-right: 48px;
				}
				button {
					pointer-events: auto;
					opacity: 1;
					cursor: pointer;
				}
			}
		}
		button[type=submit] {
			position: relative;
			display: block;
			width: 50px;
			height: 54px;
			background: none;
			border: none;
			font-size: 0;
			box-sizing: border-box;
			outline: none;
			cursor: pointer;
			&:active {
				background: rgba(#000, .2);
			}
			svg {
				color: #fff;
			}
		}
	}

	@include responsive(tablet) {
		height: $size-header-height;
		display: flex;
		&__logo {
			position: relative;
			width: 110px;
			box-sizing: border-box;
			a {
				position: absolute;
				left: 25px;
				top: 4px;
				padding: 5px;
			}
			img {
				width: 64px;
			}
		}
		&__navigation {
			position: static;
			width: auto;
			height: auto;
			margin: 0 0 0 15px;
			> button {
				display: none;
			}
			> div {
				opacity: 1;
				pointer-events: auto;
				position: static;
			}
		}
		&__search {
			width: $size-header-height;
			height: $size-header-height;
			> button {
				cursor: pointer;
			}
		}

		&-navigation {
			ul {
				position: static;
				background: none;
				padding: 0;
				flex-wrap: initial;
				&:before {
					display: none;
				}
			}
			li {
				position: static;
				width: auto;
			}
			a {
				display: flex;
				align-items: center;
				margin: 0;
				padding: 0 15px;
				height: $size-header-height;
				font-size: 13px;
				transition: background .2s ease-out;
				&:hover {
					background: rgba(#000, .1);
				}
			}
			li.on {
				a {
					//background: rgba(#000, .1);
				}
			}
		}
		&-search {
			form {
				left: auto;
				width: 340px;
			}
		}
	}
	@include dark-mode() {
		$icon-back-active-color: rgba(255,255,255,.1);
		&__navigation {
			> button {
				&:active {
					background-color: $icon-back-active-color;
				}
				img {
					opacity: .75;
				}
			}
		}
		&__search {
			> button {
				&:active {
					background-color: $icon-back-active-color;
				}
				img {
					opacity: .75;
				}
			}
		}
		&-navigation {
			ul {
				background: rgba(#000, .9);
			}
			a {
				color: $color-dark-text-base;
				&.active {

				}
			}
		}
		&-search {
			form {
				background: rgba(#000, .9);
			}
			span {
				input {
					color: $color-dark-text-base;
				}
			}
		}
		@include responsive(tablet) {
			&-navigation {
				ul {
					background: none;
				}
				li {
					&.on a {
						background: rgba($color-key, .75);
					}
				}
				a {
					&:hover {
						background-color: rgba(255,255,255,.1);
					}
				}
			}
		}
	}
}

// footer
.footer {
	padding: 50px 0 20px;
	user-select: none;
	&__copyright {
		text-align: center;
		margin: 0;
		font-size: 11px;
		color: #666;
		font-family: $font-eng;
	}

	@include responsive(tablet) {
		padding: 80px 0 24px;
	}
	@include dark-mode() {
		&__copyright {
			color: #666;
		}
	}
}

// container
.container {
	padding: $size-header-height-mobile + 32px 0 0;
	@include responsive(tablet) {
		padding: $size-header-height + 40px 0 0;
	}
}

// error
.error {
	padding: 0;
	text-align: center;

	&__image {
		margin: 0;
		img {
			display: block;
			width: 60vw;
			min-width: 240px;
			margin: 0 auto;
		}
	}
	&__code {
		margin: 10px 0 0;
		font-size: 14px;
		font-family: $font-console;
		line-height: 1;
	}
	&__message {
		margin: 5px 0 0;
		font-size: 24px;
		font-family: $font-eng;
		line-height: 1.25;
	}

	@include responsive(tablet) {
		&__image {
			img {
				width: 40vw;
				max-width: 400px;
			}
		}
	}
}