// color
$color-text-base: #222;
$color-key: #b31f37;
$color-bg: #fafbfc;
$color-code: #4f64ff;
$color-dark-text-base: #ddd;
$color-dark-bg: #222;
$color-dark-code: #4fff96;


// size
$size-header-height-mobile: 50px;
$size-header-height: 44px;
$size-screen-min: 320px;
$size-screen-mini: 640px;
$size-screen-tablet: 768px;
$size-screen-desktop: 1000px;
$size-screen-max: 1320px;

$size-container-base: 1100px;
$size-container-base-index: 1400px;

$size-container-padding: 60px;
$size-container-padding-tablet: 20px;


// font
$font-system: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
$font-eng: Helvetica, Arial, sans-serif;
$font-console: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;